@font-face {
  font-family: 'Choco Cookies Regular';
  font-style: normal;
  font-weight: normal;
  src: local('Choco Cookies Regular'), url('Choco Cookies Demo.woff') format('woff');
  }
@font-face {
font-family: 'Moontells Regular';
font-style: normal;
font-weight: normal;
src: local('Moontells Regular'), url('Moontells Demo.woff') format('woff');
}
.head{
  font-family: 'Choco Cookies Regular';
  font-size: 300%;
  color: #ffb17e;
}

body{
  font-family: Moontells Regular;
  background-color: #74EBD5;
  background-image: linear-gradient(90deg, #74EBD5 0%, #9FACE6 100%);
}

.App{
 
  text-align: center;
  background-color: #0093E9;
  background-image: linear-gradient(160deg, #0093E9 0%, #80D0C7 100%);
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
  
}

#todo-form input{
  
  background-color: rgb(245,255,250);
  border:0;
  margin: 20px;
  width: 200px;
  height: 40px;
  padding: 0 15px;
  border-radius: 5px;
}

#todo-form button{
  background-color: rgb(245,255,250);
  width: 50px;
  height: 40px;
  border-radius: 5px;
  cursor: pointer;
}
#todo-form input:focus, #todo-form button:focus{
  outline: none;
}
